<template>
  <div>
    <!--面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>每日健康管理</el-breadcrumb-item>
      <el-breadcrumb-item>健康登记</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--搜索头部input框-->
      <el-row :gutter="15">
        <el-col :span="8">
          <el-input v-model="queryInfo.username" class="input-with-select" clearable placeholder="请输入内容"
                    @clear="getRegisterList">
            <el-button slot="append" icon="el-icon-search" @click="getRegisterList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="addVisible=true">添加记录</el-button>
          <el-button type="danger" @click="batchDelete(tableChecked)">批量删除</el-button>
        </el-col>
      </el-row>
      <el-table
          :data="RegisterList"
          :cell-style="rowClass"
          :header-cell-style="headClass"
          :row-style="rowStyle"
          :row-class-name="rowStyle"
          @selection-change="handleSelectionChange"
          border
          style="width: 100%">
        <el-table-column type="selection" width="55"></el-table-column>
        <!--        <el-table-column-->
        <!--            label="ID"-->
        <!--            prop="id"-->
        <!--            width="45px">-->
        <!--        </el-table-column>-->
        <el-table-column
            label="姓名"
            prop="name">
        </el-table-column>
        <el-table-column
            label="电话"
            prop="phone"
            width="105px">
        </el-table-column>
        <el-table-column
            label="所在地"
            prop="szd"
            width="83px">
        </el-table-column>
        <el-table-column
            label="体温"
            prop="tw">
        </el-table-column>
        <el-table-column
            label="身体状况"
            prop="stzk">
        </el-table-column>
        <el-table-column
            label="是否与感染者接触"
            prop="grjc"
            width="80px">
        </el-table-column>
        <el-table-column
            label="近14天"
            prop="fourteenDay"
            :show-overflow-tooltip="true"
            width="145">
        </el-table-column>
        <el-table-column
            label="创建时间"
            prop="createTime"
            width="140px">
        </el-table-column>
        <el-table-column
            label="备注说明"
            :show-overflow-tooltip="true"
            prop="bz">
        </el-table-column>
        <el-table-column
            label="操作"
            width="150px"
        >
          <template slot-scope="scope">
            <el-tooltip class="item" content="编辑" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-edit" size="mini" type="primary"
                         @click="editRegister(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="删除" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-delete" size="mini" type="danger"
                         @click="deleteRegister(scope.row.id)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="转为隔离" effect="dark" placement="top-start">
              <
              <el-button circle icon="el-icon-plus" size="mini" type="primary"
                         @click="addDivide(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--      分页功能-->
      <el-pagination
          :current-page="queryInfo.pageNum"
          :page-size="queryInfo.pageSize"
          :page-sizes="[5,8,10,12,15]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>
    <!--添加dialog对话框-->
    <el-dialog
        :visible.sync="addVisible"
        title="添加用户"
        width="35%"
        @close="closeAddDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addFormRef" :model="Register" :rules="mobileFormRules" class="demo-user" label-width="100px">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="Register.name"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model="Register.phone"></el-input>
        </el-form-item>
        <el-form-item label="所在地" prop="szd">
          <el-input v-model="Register.szd"></el-input>
        </el-form-item>
        <el-form-item label="体温" prop="tw">
          <el-input v-model="Register.tw"></el-input>
        </el-form-item>
        <el-form-item label="身体状况" prop="stzk">
          <el-input v-model="Register.stzk"></el-input>
        </el-form-item>
        <el-form-item class="ellabel" label="是否与感染者接触" prop="grjc" style="line-height:20px">
          <el-input v-model="Register.grjc"></el-input>
        </el-form-item>
        <el-form-item label="近14天" prop="fourteenDay">
          <el-input v-model="Register.fourteenDay"></el-input>
        </el-form-item>
        <el-form-item label="备注说明" prop="bz">
          <el-input v-model="Register.bz"></el-input>
        </el-form-item>
        <el-form-item label="创建时间" prop="createTime">
          <el-date-picker
              style="float: left"
              v-model=Register.createTime
              placeholder="选择日期时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
    <el-button @click="addVisible = false">取 消</el-button>
    <el-button type="primary" @click="addRegister">确 定</el-button>
    </el-dialog>

    <!--修改dialog对话框-->
    <el-dialog
        :visible.sync="editVisible"
        title="修改信息"
        width="35%"
        @close="closeEditDialog"
    >
      <!--这是内容区域-->
      <el-form ref="editFormRef" :model="Register" :rules="mobileFormRules" class="demo-user" label-width="100px">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="Register.name"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model="Register.phone"></el-input>
        </el-form-item>
        <el-form-item label="所在地" prop="szd">
          <el-input v-model="Register.szd"></el-input>
        </el-form-item>
        <el-form-item label="体温" prop="tw">
          <el-input v-model="Register.tw"></el-input>
        </el-form-item>
        <el-form-item label="身体状况" prop="stzk">
          <el-input v-model="Register.stzk"></el-input>
        </el-form-item>
        <el-form-item class="ellabel" label="是否与感染者接触" prop="grjc" style="line-height:20px">
          <el-input v-model="Register.grjc"></el-input>
        </el-form-item>
        <el-form-item label="近14天" prop="fourteenDay">
          <el-input v-model="Register.fourteenDay"></el-input>
        </el-form-item>
        <el-form-item label="备注说明" prop="bz">
          <el-input v-model="Register.bz"></el-input>
        </el-form-item>
        <el-form-item label="创建时间" prop="createTime">
          <el-date-picker
              style="float: left"
              v-model=Register.createTime
              placeholder="选择日期时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <el-button @click="editVisible = false">取 消</el-button>
      <el-button type="primary" @click="updateRegister">确 定</el-button>
    </el-dialog>

    <!--添加dialog对话框-->
    <el-dialog
        :visible.sync="addDivideVisible"
        title="添加数据"
        width="35%"
        @close="closeAddDivideDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addFormRef" :model="Divide" :rules="mobileFormRules" class="demo-user" label-width="100px">
        <el-form-item label="姓名" prop="username">
          <el-input v-model="Divide.username"></el-input>
        </el-form-item>
        <el-form-item label="电话：" prop="mobile">
          <el-input v-model="Divide.mobile"></el-input>
        </el-form-item>
        <el-form-item label="隔离地点：" prop="area">
          <el-input v-model="Divide.area"></el-input>
        </el-form-item>
        <el-form-item label="体温：" prop="tw">
          <el-input v-model="Divide.tw" style="width: 100px;float:left"></el-input>
          <div style="float:left">°C</div>
        </el-form-item>
        <el-form-item label="症状1：" style="float:left">
          <el-checkbox v-model="Divide.status1" prop="status1" true-label="咳嗽：">咳嗽</el-checkbox>
        </el-form-item>
        <el-form-item label="症状2：" style="float:left">
          <el-checkbox v-model="Divide.status2" prop="status2" true-label="感冒">感冒</el-checkbox>
        </el-form-item>
        <el-form-item label="症状3：" style="float:left">
          <el-checkbox v-model="Divide.status3" prop="status3" style="float:left" true-label="发烧">发烧</el-checkbox>
        </el-form-item>
        <el-form-item label="备注：" prop="bz">
          <el-input v-model="Divide.bz"></el-input>
        </el-form-item>
        <el-form-item label="隔离日期：" prop="createTime">
          <el-date-picker
              v-model="Divide.createTime"
              placeholder="选择日期时间"
              size="large"
              style="float: left"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <el-button @click="addDivideVisible = false">取 消</el-button>
      <el-button type="primary" @click="ConfirmAddDivide">确 定</el-button>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    //验证手机号码的规则
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1[345789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的手机号码'))
    };
    return {
      tableChecked: [],//被选中的记录数据-----对应“批量删除”传的参数值
      id: null,
      ids: [],//批量删除id
      //  请求用户列表的参数
      queryInfo: {
        username: '',
        pageNum: 1,
        pageSize: 5,
      },
      total: 0,
      RegisterList: [],
      Register: {
        phone: ''
      },
      addVisible: false,
      editVisible: false,
      addDivideVisible: false,
      Divide: {},
      mobileFormRules: {
        phone: [
          {validator: checkMobile, trigger: 'blur'}
        ],
      },
    }
  },
  methods: {
    headClass() {  //表头居中显示
      return "text-align:center"
    },
    rowClass() { //表格数据居中显示
      return "text-align:center"
    },
    rowStyle({row}) {
      if (row.grjc === '是') {
        return {
          backgroundColor: '#FFE5EB',
        }
      }
    },
    handleSelectionChange(val) {
      this.tableChecked = val;
    },
    getRegisterList() {
      this.$axios.get('/admin/register/registerList', {params: this.queryInfo}).then(res => {
        // console.log(res.data.data.total)
        // console.log(res)
        if (res.data.data.total !== undefined) {
          if (res.data.status === 10000) {
            this.total = res.data.data.total;
            this.RegisterList = res.data.data.list;
            this.$message.success("请求健康登记表成功!")
          } else {
            this.$message.error("请求健康登记表失败！")
          }
        } else {
          if (res.data.status === 10000) {
            this.total = 1;
            this.RegisterList = [];
            this.RegisterList.push(res.data.data);
            this.$message.success("搜索成功！");
          } else {
            this.$message.error("搜索失败！")
          }
        }
      })
    },
    //当每页数据条数发生改变的额时候触发
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      this.getRegisterList();
    },
    //但每页数据条数发生改变的时候触发
    handleCurrentChange(newNum) {
      this.queryInfo.pageNum = newNum;
      this.getRegisterList();
    },
    closeAddDialog() {
      this.$refs.addFormRef.resetFields();
    },
    addRegister() {
      this.$refs["addFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {
          this.$axios.post('/register/add', this.Register).then(res => {
            if (res.data.status === 10000) {
              //关闭对话框
              this.addVisible = !this.addVisible;
              this.$message.success("新增记录成功！");
              //刷新列表
              this.getRegisterList();
            } else {
              this.addVisible = !this.addVisible;
              this.$message.error("新增记录失败！");
            }
          })
        }
      })
    },
    editRegister(row) {
      // console.log(row)
      this.Register = row;
      this.editVisible = !this.editVisible;
    },
    updateRegister() {
      this.$refs["editFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {

          this.$axios.post('/register/update', this.Register).then(res => {
            if (res.data.status === 10000) {
              this.$message.success("修改记录成功！");
              this.editVisible = !this.editVisible;
              this.getRegisterList()
            } else {
              this.editVisible = !this.editVisible;
              this.$message.error("修改记录失败！");
            }
          })
        }
      })
    },
    closeEditDialog() {
      this.$refs.editFormRef.resetFields();
    },
    deleteRegister(id) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '删除用户', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        //点击确定，发起删除用户
        this.$axios.post('/admin/register/delete/' + id).then(res => {
          if (res.data.status === 10000) {
            this.$message.success("删除用户成功！")
            this.getRegisterList();
          } else {
            this.$message.error("删除用户失败！")
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    batchDelete(rows) {
      rows.forEach((item, index) => {
        this.ids.push(item.id);
      });
      console.log(this.ids, '删除的id')
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.$axios.post('/admin/register/batchDelete', this.ids).then(res => {
          if (res.data.status !== 10000) {
            this.$message.error("删除失败！");
          }
          this.$message({type: 'success', message: '删除成功!'});
          this.getRegisterList();
          //   });
          console.log('删除了', this.ids);
          this.ids = [];
        }).catch((e) => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      })
    },
    addDivide(row) {
      this.addDivideVisible = true;
      this.Divide.username = row.name;
      this.Divide.mobile = row.phone;
      this.id = row.id;
    },
    ConfirmAddDivide() {
      this.Divide.tw = this.Divide.tw + "°C";
      console.log(this.Divide)
      this.$axios.post('/divide/add', this.Divide).then(res => {
        if (res.data.status === 10000) {
          this.$axios.post('/admin/register/delete/' + this.id).then(res => {
            if (res.data.status === 10000) {
              this.$message.success("转移成功！")
              this.getRegisterList();
            } else {
              this.$message.error("转移失败！删除出错")
              this.getRegisterList();
            }
          })
        } else {
          this.$message.error("转移失败！添加到隔离人员失败！")
          this.getRegisterList();
        }
        this.addDivideVisible = false;
      })
    },
    closeAddDivideDialog() {
      this.$refs.addFormRef.resetFields();
    }
  },
  created() {
    //进页面之前就获取用户列表
    this.getRegisterList();
  }
}
</script>

<style scoped>

</style>
